const resetCommonModal = () => {
    $('#commonModal')
        .find('#name')
        .val('');
    $('#commonModal')
        .find('#extension')
        .prop('disabled', true)
        .val('');
    $('#commonModal')
        .find('#type')
        .val('folder');
};
const setValueForCommonModal = (functionName, type, name, extension, id) => {
    $('#commonModal').modal('show');
    $('#commonModal')
        .find('#commonModalLabel')
        .text(`${functionName} ${type}`);
    $('#commonModal')
        .find('#type')
        .val(type)
        .prop('disabled', functionName === 'Edit');
    $('#commonModal')
        .find('#name')
        .val(name);
    $('#commonModal')
        .find('#extension')
        .prop('disabled', type === 'folder')
        .val(extension || '');
    $('#commonModal')
        .find('#id')
        .val(id || '');
};
const setLoadingForSubmitButton = (isLoading) => {
    $('#commonModal')
        .find('#btnSubmit')
        .prop('disabled', isLoading)
        .text(isLoading ? 'Loading...' : 'Submit');
};
const modalHelper = {
    resetCommonModal,
    setValueForCommonModal,
    setLoadingForSubmitButton,
};
export default modalHelper;
